import React from "react";
import { motion } from "framer-motion";
import { ReactComponent as Logo } from "./image/CHLLogo.svg";
import { Link } from "react-router-dom";

import CVPdf from "./assets/CheungHeiLong_curriculumVitae.pdf"

import "./css/landing.min.css";
const Landing = () => {
	const textVariants = {
		init: {
			opacity: 0,
			x: 100,
		},
		anim: {
			opacity: 1,
			x: 0,
		},
	};
	const btnGroupVariants = {
		init: {
			opacity: 0,
		},
		anim: {
			opacity: 1,
		},
	};

	return (
		<motion.div
			variants={window.$containerVariants}
			initial='init'
			animate='anim'
			exit='exit'
			className='landingContentContainer'>
			<div className='bgLogoContainer'>
				<Logo className='bgLogo' alt='logo' />
			</div>
			<div className='nameBlockContainer'>
				<div className='emptyBlock'></div>
				<div className='nameBlock'>
					<motion.h1
						variants={textVariants}
						transition={{ duration: 0.5 }}
						id='title'
						className='title'>
						Cheung Hei Long
					</motion.h1>
					<motion.h5
						variants={textVariants}
						transition={{ delay: 0.5, duration: 0.5 }}
						id='subTitle'
						className='subTitle'>
						Fresh Graduate on my way to becoming a professional
						Full-stack developer
					</motion.h5>
					<motion.div
						className='landingBtnGroup'
						variants={btnGroupVariants}
						transition={{ delay: 1, duration: 0.5 }}>
						<a
							id='CvBtn'
							type='button'
                            className='button btn portfolioBtn'
                            href={CVPdf}
                            target="_blank">
							DOWNLOAD CV
						</a>
						<Link to={"/portfolio"}>
							<button
								id='portfolioBtn'
								type='button'
								className='btn portfolioBtn'>
								Go to my portfolio
								<i className='fas fa-arrow-right'></i>
							</button>
						</Link>
					</motion.div>
				</div>
			</div>
		</motion.div>
	);
};

export default Landing;
