import React from "react";
import { motion } from "framer-motion";

import "../css/skillContainer.min.css";
const SkillContainer = (props) => {
	let skill = props.skill;
	let skillType = "";
	if (skill.type === "FE") skillType = "feSkill";
	else if (skill.type === "BE") skillType = "beSkill";
	else if (skill.type === "OT") skillType = "otSkill";
	else if (skill.type === "SW") skillType = "swSkill";

	return (
		<motion.span className={`skillContainer ${skillType}`}>
			{skill.name}
		</motion.span>
	);
};

export default SkillContainer;
