import React from "react";
import ReactDOM from "react-dom";
import { HashRouter as Router } from "react-router-dom";
import App from "./App";

window.$containerVariants = {
    init: {
        opacity: 0,
    },
    anim: {
        opacity: 1,
        transition: {
            delay: 0,
            duration: 1,
        },
    },
    exit: {
        x: "100vw",
        transition: { ease: "easeInOut" },
    },
};

ReactDOM.render(
	<Router>
		<App />
	</Router>,
	document.getElementById("root")
);
