import React from "react";
import ReactDom from "react-dom";
import ImageGallery from "react-image-gallery";
import { motion } from "framer-motion";

import SkillContainer from "./skillContainer";

import "react-image-gallery/styles/css/image-gallery.css";

const ProjectModal = (props) => {
	const {
		projectTitle,
		projectName,
		projectType,
		folder,
		skills,
		imgList,
		projectDescription,
	} = props.projectInfo;

	const getImages = require.context("../image/portfolio/", true);
	const images = [];
	imgList.forEach((imgName) => {
		let tempImgPath = `./${folder}/`;
		let tempImgObject = {
			original: getImages(tempImgPath + imgName),
			thumbnail: getImages(tempImgPath + imgName),
		};
		images.push(tempImgObject);
	});

	const modalVariants = {
		init: {
			opacity: 0,
			y: 500,
		},
		anim: {
			opacity: 1,
			y: 0,
		},
	};

	const clickHideModal = (event) => {
		if (event.target?.id === "modalOverlay") props.hideModal();
	};
	if (props.modalShow) {
		return ReactDom.createPortal(
			<>
				<div
					className='modalOverlay'
					id='modalOverlay'
					onClick={(event) => clickHideModal(event)}>
					<motion.div
						className='modalContainer'
						variants={modalVariants}
						initial='init'
						animate='anim'>
						<div className='modalHeader'>
							<div className='burger' onClick={props.hideModal}>
								<div className={`line1`}></div>
								<div className={`line3`}></div>
							</div>
						</div>
						<div className='modalContent'>
							<div className='title-black'>{projectName}</div>
							<div className='subTitle-black projectSubTitle'>
								{projectTitle}
							</div>
							<div className='imageGalleryContainer'>
								<ImageGallery
									items={images}
									showBullets={true}
									lazyLoad={true}
									showThumbnails={false}
									showPlayButton={false}
								/>
							</div>
							<div className='contentInfoLineContainer'>
								<span className='contentInfoTitle'>Type:</span>
								<span className='contentInfoText'>
									{" " + projectType}
								</span>
							</div>
							<div className='contentInfoLineContainer'>
								<span className='contentInfoTitle'>
									Related technology:
								</span>
								<span className='skillInfoText'>
									{skills.map((skill, index) => (
										<SkillContainer
											key={`skill-${index}`}
											skill={skill}
										/>
									))}
								</span>
							</div>
							<div className='contentInfoLineContainer'>
								<span className='contentInfoTitle'>
									Description:
								</span>
								<span className='skillInfoText'>
									{projectDescription}
								</span>
							</div>
						</div>
					</motion.div>
				</div>
			</>,
			document.getElementById("portal")
		);
	} else {
		return <></>;
	}
};

export default ProjectModal;
