import React from "react";
import { motion } from "framer-motion";
import "./css/about.min.css";
const About = () => {
	return (
		<motion.div
			variants={window.$containerVariants}
			initial='init'
			animate='anim'
			exit='exit'>
			<h1 id='pageTitle' className='pageTitle'>
				About Me
			</h1>
		</motion.div>
	);
};

export default About;
