import React from "react";
import "./css/contact.min.css";

import { motion } from "framer-motion";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faWhatsappSquare,
	faLinkedinIn,
	faInstagram,
} from "@fortawesome/free-brands-svg-icons";
import {
	faPhoneSquareAlt,
	faEnvelopeSquare,
} from "@fortawesome/free-solid-svg-icons";
import Footer from "./component/footer";

const contactSectionVariants = {
	init: {
		y: 250,
		opacity: 0,
	},
	anim: {
		y: 0,
		opacity: 1,
	},
};

const Contact = () => {
	const btnLink = (target) => {
		if (target === "linkedin")
			window.open(
				"https://www.linkedin.com/in/hei-long-cheung",
				"_blank"
			);
		else if (target === "instagram")
			window.open("https://www.instagram.com/cheung__long/", "_blank");
		return null;
	};
	return (
		<>
			<motion.div
				className='contactContentContainer'
				variants={window.$containerVariants}
				initial='init'
				animate='anim'
				exit='exit'>
				<h1 id='pageTitle' className='pageTitle'>
					Contact
				</h1>
				<div className='contactSectionContainer container'>
					<div className='contactSection phoneSectionContainer'>
						<motion.div
							className='phoneSection'
							variants={contactSectionVariants}
							initial='init'
							animate='anim'
							transition={{ duration: 0.5 }}>
							<h2>Contact me by</h2>
							<div className='phoneSectionLine'>
								<FontAwesomeIcon
									icon={faPhoneSquareAlt}
									size='lg'
								/>{" "}
								+852 6387 9636
							</div>
							<div className='phoneSectionLine'>
								<FontAwesomeIcon
									icon={faWhatsappSquare}
									size='lg'
								/>{" "}
								+852 6387 9636
							</div>
							<div className='phoneSectionLine'>
								<FontAwesomeIcon
									icon={faEnvelopeSquare}
									size='lg'
								/>{" "}
								hlcheung510@gmail.com
							</div>
							<div className='buttonGroup'>
								<button onClick={() => btnLink("linkedin")}>
									<FontAwesomeIcon
										icon={faLinkedinIn}
										size='lg'
									/>{" "}
									Linkedin
								</button>
								<button onClick={() => btnLink("instagram")}>
									<FontAwesomeIcon
										icon={faInstagram}
										size='lg'
									/>{" "}
									Instagram
								</button>
							</div>
						</motion.div>
					</div>
					<motion.div
						className='contactSection formSection'
						variants={contactSectionVariants}
						initial='init'
						animate='anim'
						transition={{ duration: 0.5, delay: 0.5 }}>
						<h2>Or send me a message now</h2>
						<div className='contactForm'>
							<form
								id='contactForm'
								action='https://formspree.io/hlcheung510@gmail.com'
								method='POST'>
								<div className='inputGroup'>
									<label htmlFor='senderName'>Name:</label>
									<input
										type='text'
										name='senderName'
										id='senderName'
										placeholder='your name'
									/>
								</div>
								<div className='inputGroup'>
									<label htmlFor='senderEmail'>
										Email address:
									</label>
									<input
										type='email'
										name='_replyto'
										id='senderEmail'
										placeholder='your email address'
									/>
								</div>
								<div className='inputGroup'>
									<label htmlFor='senderMessage'>
										Message
									</label>
									<textarea
										name='senderMessage'
										id='senderMessage'
										cols='30'
										rows='10'
										placeholder='message you want to tell me'></textarea>
								</div>
								<button className='btn-primary' type='submit'>
									submit
								</button>
							</form>
						</div>
					</motion.div>
				</div>
			</motion.div>
			<Footer />
		</>
	);
};

export default Contact;
