import React from "react";
import { motion } from "framer-motion";
import Footer from "./component/footer";
import SkillContainer from "./component/skillContainer";
import "./css/portfolio.min.css";
import ProjectBlock from "./component/projectBlock";

const Portfolio = () => {
	const fypInfo = {
		projectTitle: "Block-based python learning platform",
		projectName: "Final Year Project",
		projectType: "Web Platform",
		skills: [
			{ name: "React", type: "FE" },
			{ name: "Redux", type: "FE" },
			{ name: "Bootstrap", type: "FE" },
			{ name: "HTML", type: "FE" },
			{ name: "CSS", type: "FE" },
			{ name: "JavaScript", type: "OT" },
			{ name: "Axios", type: "OT" },
			{ name: "JSON Web Token", type: "OT" },
			{ name: "Node.js", type: "BE" },
			{ name: "Express", type: "BE" },
			{ name: "SQL", type: "BE" },
		],
		projectDescription: (
			<div className='contentInfoText'>
				<b>Block-based python learning platform</b> is a STEM education
				platform that allows users to write their own python code by
				drag and dropping code block. The platform allow users to export
				and download the project save file for next time use or export
				the translated Python file. Logged-in users can also choose to
				save their projects in the online repository.
				<br />
				<br />
				Also, the lecture system allows the instructor to write their
				own lecture note with the editor provided by the platform
				directly and publish to a specific student group. Then the
				student who belong to the group can read the note in the
				platform any time anywhere. The instructor can assign and
				collect homework though the platform to have a better follow up
				on student's learning status too.
			</div>
		),
	};
	const projectInfos = [
		{
			projectTitle: "NeedConnect",
			projectName: "SDG Asia Innotech Challenge 2020",
			projectType: "Web platform",
			folder: "S7",
			mainImg: "Image 282.png",
			skills: [
				{ name: "React", type: "FE" },
				{ name: "Bootstrap", type: "FE" },
				{ name: "HTML", type: "FE" },
				{ name: "SCSS", type: "FE" },
				{ name: "JavaScript", type: "OT" },
				{ name: "Framer Motion", type: "OT" },
			],
			imgList: [
				"Image 278.png",
				"Image 282.png",
				"Image 280.png",
				"Image 281.png",
			],
			projectDescription: (
				<div>
					<b>"NeedConnect"</b> is a web application developed in the
					SDG Asia Innotech Challenge 2020 24 hours Hackathon event.
					The Application's aim is to resolve the resource mismatch
					around the world and fulfilling the "Good Health and
					Well-being" sustainable development goal. The application
					contains a map to visualize the need for a different region,
					allow charity groups to host resources delivery events for
					volunteers to join freely, allow the donor to donate the
					resource to the region they want, and track the resource
					delivery transparently.
				</div>
			),
		},
		{
			projectTitle: "Weather app",
			projectName: "Data visualization Project",
			projectType: "Web platform",
			folder: "S8",
			mainImg: "mainImg.png",
			skills: [
				{ name: "HTML", type: "FE" },
				{ name: "SCSS", type: "FE" },
				{ name: "D3.js", type: "OT" },
				{ name: "JavaScript", type: "OT" },
				{ name: "Axios", type: "OT" },
			],
			imgList: [
				"Image 262.png",
				"Image 263.png",
				"Image 265.png",
				"Image 264.png",
			],
			projectDescription: (
				<div>
					<b>"Weather app"</b> is a responsive web application that
					provides visualized weather information. The application
					receives data from the Hong Kong Observatory API, providing
					current weather information, weather forecast, weather
					alert, etc.
				</div>
			),
		},
		{
			projectTitle: "大笪地社交平台",
			projectName: "HONG KONG THEI, CARITAS JOINT PROJECT",
			projectType: "Web platform",
			folder: "S2",
			mainImg: "mainImg.png",
			skills: [
				{ name: "Bootstrap", type: "FE" },
				{ name: "HTML", type: "FE" },
				{ name: "CSS", type: "FE" },
				{ name: "JavaScript", type: "OT" },
				{ name: "Jquery", type: "OT" },
				{ name: "PHP", type: "OT" },
				{ name: "SQL", type: "BE" },
			],
			imgList: [
				"S2img02.png",
				"S2img01.png",
				"S2img00.png",
				"S2img03.png",
			],
			projectDescription: (
				<div>
					<b>"大笪地社交平台"</b> is an online social media platform
					allowing HKPA members to communicate with each other when
					the HKPA center is closed. The platform allows HKPA staff to
					post the information about their latest activity,
					advertisements for the monthly bazaar event, and update the
					event schedule into the calendar. Members can also write
					posts to share photos and recipes with other members.
				</div>
			),
		},
		{
			projectTitle: "Clone Battle",
			projectName: "Unity Game Project",
			projectType: "Unity game",
			folder: "S4",
			mainImg: "mainImg.png",
			skills: [{ name: "Unity", type: "SW" }],
			imgList: [
				"S4img05.png",
				"S4img04.png",
				"S4img00.png",
				"S4img03.png",
				"S4img01.png",
				"S4img02.png",
			],
			projectDescription: (
				<div>
					<b>"Clone Battle"</b> is a 3D Role-Playing-Game that
					contains three scenes with the theme of forest, cargo ship,
					and city. The player is required to reach the location of
					three specific goals to win the game. The game development
					project is a group project and I am responsible to design
					the game logic, develop the character controller, assign
					tasks to other members, and oversee the project progress as
					a project manager.
				</div>
			),
		},
		{
			projectTitle: "智者行",
			projectName: "Mobile Application Development project",
			projectType: "Android application",
			folder: "S5",
			mainImg: "mainImg.png",
			skills: [{ name: "Android Studio", type: "SW" }],
			imgList: [
				"S5img00.png",
				"S5img01.png",
				"S5img02.png",
				"S5img03.png",
			],
			projectDescription: (
				<div>
					<b>"智者行"</b> is an Android application designed for
					elderly to facilitate their daily living. The application
					integrated easy access to useful features of the mobile
					phone into one single application. The application also
					provides a first aid guide and one-click seek help feature
					in an emergency situation.
				</div>
			),
		},
		{
			projectTitle: "日常猜與揀",
			projectName: "HONG KONG THEI, CARITAS JOINT PROJECT",
			projectType: "Application",
			folder: "S6",
			mainImg: "S6img002.png",
			skills: [{ name: "GameSalad", type: "SW" }],
			imgList: ["S6img00.png", "S6img01.png", "S6img02.png"],
			projectDescription: (
				<div>
					<b>"日常猜與揀"</b> is an application developed for Caritas
					Lok Ngai / Lok Yin Day Activity Center to facilitate their
					daily operation. The application provides a quiz game for
					the training of disabled and a voting feature.
				</div>
			),
		},
	];
	const fypContainerVariants = {
		init: {
			opacity: 0,
			x: 300,
		},
		anim: {
			opacity: 1,
			x: 0,
			transition: {
				when: "beforeChildren",
				duration: 0.5,
				type: "spring",
				stiffness: 100,
			},
		},
	};
	const childVariants = {
		init: {
			opacity: 0,
			y: 500,
		},
		anim: {
			opacity: 1,
			y: 0,
		},
	};
	return (
		<>
			<motion.div
				className='portfolioContentContainer'
				variants={window.$containerVariants}
				initial='init'
				animate='anim'
				exit='exit'>
				<h1 id='pageTitle' className='pageTitle'>
					Portfolio
				</h1>
				<motion.div
					className='fypContainer'
					variants={fypContainerVariants}>
					<div className='title-black fypTitle'>
						{fypInfo.projectName}
					</div>
					<div className='subTitle-black fypSubTitle'>
						{fypInfo.projectTitle}
					</div>
					<div className='fypContentContainer'>
						<div className='fypVideoContainer'>
							<div className='fypVideoIFrameWrapper'>
								<iframe
									title='fypVideoIFrame'
									className='fypVideoIFrame'
									src='https://www.youtube.com/embed/0yTI0TcRNnw'
									vq='hd1080'
									frameBorder='0'
									loading='lazy'
									srcDoc="<style>*{padding:0;margin:0;overflow:hidden}html,body{height:100%}img,span{position:absolute;width:100%;top:0;bottom:0;margin:auto}span{height:1.5em;text-align:center;font:48px/1.5 sans-serif;color:white;text-shadow:0 0 0.5em black}</style><a href=https://www.youtube.com/embed/0yTI0TcRNnw?autoplay=1><img src=https://img.youtube.com/vi/0yTI0TcRNnw/hqdefault.jpg alt='FYP - Block-Based Python Learning Platform'><span>▶</span></a>"
									allow='accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture'
									allowFullScreen='allowfullscreen'></iframe>
							</div>
						</div>
						<div className='fypContent'>
							<div className='contentInfoLineContainer'>
								<span className='contentInfoTitle'>Type:</span>
								<span className='contentInfoText'>
									{" " + fypInfo.projectType}
								</span>
							</div>
							<div className='contentInfoLineContainer'>
								<span className='fypContentInfoTitle'>
									Related technology:
								</span>
								<span className='fypSkillInfoText'>
									{fypInfo.skills.map((skill, index) => (
										<SkillContainer
											key={`skill-${index}`}
											skill={skill}
										/>
									))}
								</span>
							</div>
							<div className='contentInfoLineContainer'>
								<div className='contentInfoTitle fypDescription'>
									Description:
								</div>
								{fypInfo.projectDescription}
							</div>
						</div>
					</div>
				</motion.div>
				<h2 className='pageTitle'>Other projects</h2>
				<div className='pageSubTitle'>
					Click the image for more details
				</div>
				<motion.div
					className='projectBlockBoard'
					variants={childVariants}
					transition={{
						delay: 1,
						duration: 1,
						type: "spring",
					}}>
					{projectInfos.map((projectInfo, index) => (
						<ProjectBlock
							key={`project-${index}`}
							projectInfo={projectInfo}
						/>
					))}
				</motion.div>
			</motion.div>
			<Footer />
		</>
	);
};

export default Portfolio;
