import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "../css/nav.min.css";
const Nav = (props) => {
	const [isOpen, setIsOpen] = useState(false);
	const [transitionStop, setTransitionStop] = useState(false);
	const [currentPage, setCurrentPage] = useState(-1);

	const contentSwitch = (page) => {
		if (page === "/" || page === "#/index") setCurrentPage(0);
		else if (page === "#/about") setCurrentPage(1);
		else if (page === "#/portfolio") setCurrentPage(2);
        else if (page === "#/contact") setCurrentPage(3);
	};
	useEffect(() => {
		let resizeTimer;
		const handleResize = () => {
			setTransitionStop(true);
			clearTimeout(resizeTimer);
			resizeTimer = setTimeout(() => setTransitionStop(false), 400);
		};
		window.addEventListener("resize", handleResize);
        contentSwitch(window.location.hash);
	});
	useEffect(() => {
		setIsOpen(false);
	},[currentPage]);
	return (
		<nav>
			<h2>
				<Link to={"/index"} onClick={() => contentSwitch("/index")}>
					Cheung Hei Long
				</Link>
			</h2>
			<div className='burger' onClick={() => setIsOpen(!isOpen)}>
				<div className={`line1 ${isOpen ? "toggle" : ""}`}></div>
				<div className={`line2 ${isOpen ? "toggle" : ""}`}></div>
				<div className={`line3 ${isOpen ? "toggle" : ""}`}></div>
			</div>
			<div
				className={`nav-link-list ${isOpen ? "active" : ""} ${
					transitionStop ? "resize-animation-stopper" : ""
				}`}>
				<div className='nav-item'>
					<Link
						to={"/index"}
						onClick={() => contentSwitch("/index")}
						className={`nav-link ${
							currentPage === 0 ? "active" : ""
						}`}>
						Home
						<div className='baseLine'></div>
					</Link>
				</div>
				{/* <div className='nav-item'>
					<Link
						to={"/about"}
						onClick={() => contentSwitch("/about")}
						className={`nav-link ${
							currentPage === 1 ? "active" : ""
						}`}>
						About Me
						<div className='baseLine'></div>
					</Link>
				</div> */}
				<div className='nav-item'>
					<Link
						to={"/portfolio"}
						onClick={() => contentSwitch("/portfolio")}
						className={`nav-link ${
							currentPage === 2 ? "active" : ""
						}`}>
						Portfolio
						<div className='baseLine'></div>
					</Link>
				</div>
				<div className='nav-item'>
					<Link
						to={"/contact"}
						onClick={() => contentSwitch("/contact")}
						className={`nav-link ${
							currentPage === 3 ? "active" : ""
						}`}>
						Contact
						<div className='baseLine'></div>
					</Link>
				</div>
			</div>
		</nav>
	);
};

export default Nav;
